import * as React from 'react';
import {
  Button,
  TextField,
  Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle
} from '@mui/material';

export type SingleEntryFormResponse = {
  value: string
};

export type SingleEntryFormProps = {
  title: string,
  description?: string,
  inputPlaceholder?: string,
  inputType?: string,
  isOpen: boolean,
  autocomplete?: boolean,
  rows?: number
  onClose: (isProceed: boolean, data?: SingleEntryFormResponse) => void
};

const defaultValues = {
  title: 'Untitled Form',
  description: '',
  inputPlaceholder: 'Enter text',
  inputType: 'text',
  autocomplete: false,
  isOpen: false
};

const SingleEntryForm: React.FC<SingleEntryFormProps> = (props) => {
  const {
    title,
    description,
    inputPlaceholder,
    inputType,
    autocomplete,
    rows = 1,
    isOpen,
    onClose
  } = props;
  const [TextFieldValue, setTextFieldValue] = React.useState('');
  const [open, setOpen] = React.useState(isOpen);

  const handleClose = (isProceed: boolean) => {
    if (!isProceed) {
      onClose(false);
      setOpen(false);
      return;
    }

    onClose(true, { value: TextFieldValue });
    setOpen(false);
  };

  const handleTextFieldChange = (e: any) => {
    setTextFieldValue(e.target.value);
  }

  const reset = () => {
    setTextFieldValue('');
  }

  React.useEffect(() => {
    reset();
    setOpen(isOpen);
  }, [isOpen])

  return (
    <div>
      <Dialog
        open={open}
        disableEscapeKeyDown={true}>
        <DialogTitle
          sx={{
            minWidth: '345px',
            width: '75vw',
          }}
        >{title}</DialogTitle>
        <DialogContent>
          { description &&
          <DialogContentText>
            {description}
          </DialogContentText>
          }
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label={inputPlaceholder || defaultValues.inputPlaceholder}
            type={inputType || defaultValues.inputType}
            fullWidth
            variant="outlined"
            autoComplete={autocomplete ? 'on' : 'off'}
            spellCheck="false"
            multiline={rows > 1}
            rows={rows}
            onChange={handleTextFieldChange}
            value={TextFieldValue}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(false)}>Cancel</Button>
          <Button onClick={() => handleClose(true)}>Save</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default SingleEntryForm;
