import * as React from 'react';
import { Grid, Stack, Typography, useMediaQuery, } from '@mui/material';
import { useQueryParam, StringParam } from 'use-query-params';

import NotebookCard from '../../components/NotebookCard';
import { CircularLoader } from '../../components/ProgressIndicators';
import { AddButton } from '../../components/Buttons';
import { SingleEntryForm, SingleEntryFormResponse } from '../../components/FormDialogs';

import { getNotebooks, saveNotebook } from '../../services/NotebookService'
import { Notebook } from '../../types/Notebook';


const Prepare: React.FC = () => {
    const [action, setAction] = useQueryParam('action', StringParam);
    const [notebooks, setNotebooks] = React.useState<Notebook[]>([]);
    const [isLoading, setLoading] = React.useState(true);
    const isMdScreen = useMediaQuery((theme: any) => theme.breakpoints.down('md'));

    const handleNewNotebook = () => {
        setAction('new');
    }

    const isValidInput = (response?: SingleEntryFormResponse) => {
        return response && response.value;
    }

    const handleClose = (isProceed: boolean, response?: SingleEntryFormResponse) => {
        if (isProceed && isValidInput(response)) {
            const notebookName = response?.value || '';
            saveNotebook({
                title: notebookName
            });
            loadNotebooks();
        }
        setAction(null);
    }

    const loadNotebooks = async () => {
        setLoading(true);

        try {
            const notebooks = await getNotebooks();
            setNotebooks(notebooks);
            setLoading(false);
        } catch(e) {
            console.log('Something went wrong:', e);
            setLoading(false);
        }
    }

    React.useEffect(() => {
        loadNotebooks();
    }, []);

    return (
        <React.Fragment>
            <Stack direction={ isMdScreen ? 'column' : 'row' } spacing={2} >
                <Typography variant="h4" component="div">
                    Prepare Notebooks
                </Typography>
                <Typography>
                    <AddButton onClickHandler={handleNewNotebook} isDisabled={isLoading} label="New Notebook" />
                </Typography>
            </Stack>
            { !isLoading ? <Grid container alignItems="stretch" spacing={4}  sx={{ marginTop: '12px', display: "flex" }}>
                {notebooks.map((notebook, idx) => {
                    return (
                        <Grid item key={idx} xs={12} sm={12} md={6} lg={4} sx={{ display: 'flex', justifyContent: 'center' }}>
                            <NotebookCard {...notebook} isDisabled={isLoading} />
                        </Grid>
                    )
                })}
            </Grid> : <CircularLoader /> }
            <SingleEntryForm
                title="New Notebook"
                description="Enter a notebook name to make it easy to identify."
                inputPlaceholder="Notebook Name"
                inputType="text"
                isOpen={action === 'new'}
                onClose={handleClose}
            />
        </React.Fragment>
    );
}

export default Prepare;
