import React from 'react';
import { Routes, Route } from 'react-router-dom';

import './App.css';

import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';

import Home from './pages/Home';
import SignIn from './pages/SignIn';
import SignUp from './pages/SignUp';
import Dashboard from './pages/Dashboard';
import {
  Prepare,
  AllNotebooks,
  ViewNotebook
} from './pages/Prepare';

// Replace this with your actual authentication check logic
const isAuthenticated = (): boolean => {
  // For demonstration purposes, always return true
  return false;
};

function App() {
  return (
    <Routes>
      <Route path="/" Component={() => {
        if (isAuthenticated()) {
          return <Home />;
        }

        return <SignIn />;
      }} />
      <Route path="/login" element={<SignIn />} />
      <Route path="/register" element={<SignUp />} />
      <Route element={<Dashboard />} >
        <Route path="/dashboard" Component={() => <h1>Dashboard</h1>} />
        <Route path="/prepare" element={<Prepare />}>
          <Route path="notebooks" element={<AllNotebooks />} />
          <Route path="notebooks/:notebookId" element={<ViewNotebook />} />
        </Route>
        <Route path="/review" Component={() => <h1>Review</h1>} />
        <Route path="/reports" Component={() => <h1>Reports</h1>} />
        <Route path="/signout" Component={() => <h1>Sign Out</h1>} />
      </Route>
    </Routes>
  )
}

export default App;
