import * as React from 'react';
import { Button }  from '@mui/material';
import { AddCircle } from '@mui/icons-material';

import { ButtonProps } from './types';

const AddButton = ({ label, onClickHandler, isDisabled }: ButtonProps) => {
    return (
        <Button
            sx={{ minWidth: '170px', margin: '0px 8px 8px 0px' }}
            variant="contained"
            endIcon={<AddCircle />}
            onClick={onClickHandler}
            disabled={isDisabled}>
            {label}
        </Button>
    )
};

export default AddButton;
