import * as React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
    Chip, Grid, Stack, Typography,
    FormGroup, FormControlLabel, Switch,
    useMediaQuery,
} from '@mui/material';
import { useQueryParam, StringParam } from 'use-query-params';

import { QuestionsTable } from '../../components/DataTables';
import { CircularLoader } from '../../components/ProgressIndicators';
import { BlankQuestionEntryForm, BlankQuestionEntryFormResponse } from '../../components/FormDialogs';
import { AddButton, BackButton } from '../../components/Buttons';

import { getNotebookById, saveQuestion } from '../../services/NotebookService';
import { Notebook } from '../../types/Notebook';

type ViewNotebookProps = {

}

const ViewNotebook: React.FC<ViewNotebookProps> = (props) => {
    const [currentNotebook, setCurrentNotebook] = React.useState<Notebook | undefined>(undefined);
    const [action, setAction] = useQueryParam('action', StringParam);
    const [isLoading, setLoading] = React.useState<boolean>(true);
    const [isAnswerShown, setAnswerShown] = React.useState<boolean>(true);
    const isMdScreen = useMediaQuery((theme: any) => theme.breakpoints.down('md'));
    const { notebookId } = useParams();
    const navigate = useNavigate();

    const handleNew = () => {
        setAction('new');
    }

    const isValidInput = (response?: BlankQuestionEntryFormResponse) => {
        if (!response) return false; // TODO: Throw error
        if (!response.selections || response.selections.length === 0) return false; //TODO: Throw error
        if (!response.text) return false; // TODO: Throw error

        return true;
    }

    const handleClose = async (isProceed: boolean, response?: BlankQuestionEntryFormResponse) => {

        if (!currentNotebook) return;

        if (isProceed && isValidInput(response)) {
            if (!response) return;
            const notebook = await saveQuestion(currentNotebook, response);
            if (!!notebook) {
                loadData();
            }
        }
        setAction(null);
    }

    const loadData = async () => {
        if (!notebookId) return;

        setLoading(true);

        const notebook = await getNotebookById(notebookId);
        if (!notebook) return; // ignore undefined
        setCurrentNotebook({...notebook});
        setLoading(false);
    }

    React.useEffect(() => {
        loadData();
    }, [notebookId]);

    return (
        <React.Fragment>
            <Grid>
                <BackButton label="Back to Notebooks" onClickHandler={() => navigate('/prepare/notebooks')} isDisabled={isLoading} />
            </Grid>
            <Stack direction={ isMdScreen ? 'column' : 'row' } spacing={2} >
                <Typography variant="h4" component="div">
                    {currentNotebook?.title}
                </Typography>
                <Typography>
                    <AddButton onClickHandler={() => handleNew()} isDisabled={isLoading} label="New Question" />
                </Typography>
            </Stack>
            <Stack>
                { isLoading ? <CircularLoader /> : null }
                { currentNotebook ?
                    <Stack direction="row" spacing={1} sx={{ margin: '4px 0 8px 0' }}>
                        {
                            currentNotebook.categories &&
                            currentNotebook.categories.map((category, idx) => <Chip key={idx} label={category.name} size="small" variant="outlined" />)
                        }
                    </Stack> : null }
                    <FormGroup>
                        <Grid sx={{ margin: '8px 0 8px 12px' }}>
                            <FormControlLabel
                                control={<Switch defaultChecked value={isAnswerShown} onClick={() => setAnswerShown(!isAnswerShown)} size="small" />}
                                label={<Typography fontSize="12px">Show Answers</Typography>} />
                        </Grid>
                    </FormGroup>
                { currentNotebook ? <QuestionsTable displayBlanks={!isAnswerShown} notebook={currentNotebook} /> : null }
            </Stack>
            <BlankQuestionEntryForm
                title="New Question"
                inputPlaceholder="Question"
                inputType="text"
                isOpen={action === 'new'}
                onClose={handleClose}
                rows={10}
            />
        </React.Fragment>
    );
}

export default ViewNotebook;
