import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import {
    Button,
    Card, CardActions, CardContent, CardMedia,
    Stack, Chip,
    Typography
} from '@mui/material';

import { Category } from '../../types/Category';

type NotebookCardProps = {
    id?: string,
    title: string,
    categories?: Category[],
    isDisabled?: boolean
};

const NotebookCard:React.FC<NotebookCardProps> = (props) => {
    const { title, categories, id, isDisabled = false } = props;
    const navigate = useNavigate();

    const handleView = () => {
        navigate(`${id}`);
    }

    return (
        <Card sx={{
            minWidth: '300px',
            width: '100%',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between'
        }}>
        {/* <CardMedia
            sx={{ height: 140, backgroundPosition: 'top' }}
            image="/notebook-header.jpg"
            title=""
        /> */}
        <CardContent sx={{ flex: ""}}>
            {/* <Stack spacing={1}> */}
                <Typography gutterBottom variant="h5" component="div">
                {title}
                </Typography>
                <Stack direction="row" spacing={1} sx={{ marginTop: '4px' }}>
                    {categories && categories.map((category, idx) => <Chip key={idx} label={category.name} size="small" variant="outlined" />)}
                </Stack>
                {/* <Typography variant="body2" color="text.secondary">
                This notebook has 32 questions.
                </Typography> */}
            {/* </Stack> */}
        </CardContent>
        <CardActions
            disableSpacing
            sx={{ display: "flex", justifyContent: "flex-end" }}>
            <Button size="small" disabled={isDisabled}>Edit</Button>
            <Button size="small" disabled={isDisabled} onClick={() => handleView()}>View</Button>
            {/* <Button size="small">Generate Reviewer</Button> */}
        </CardActions>
        </Card>
    );
}

export default NotebookCard;
