let currentId = 0;
const _newId = () => {
    currentId += 1;
    return `new` + currentId;
};


export const getNotebooks = (withQuestions: boolean = false) : Promise<Notebook[]> => {
    const _notebooks = notebooks.sort((a, b) => sortByDateDesc(a.date_created, b.date_created));
    return new Promise((resolve, reject) => {
        setTimeout(() => {
            return resolve(_notebooks);
        }, 750);
    });
};

export const getNotebookById = (id: string) : Promise<Notebook | undefined> => {
    const notebook = notebooks.find(x => x.id === id);

    // sort questions
    if (!!notebook && !!notebook.questions) {
        notebook.questions.sort((a, b) => sortByDateDesc(a.date_created, b.date_created));
    }

    return new Promise((resolve, reject) => {
        setTimeout(() => {
            return resolve(notebook);
        }, 750);
    });
};

export const saveNotebook = (notebook: Notebook) => {
    const { title, categories } = notebook;
    const newId = _newId();
    const newNotebook = {
        id: newId,
        title,
        categories,
        date_created: new Date(),
    };
    notebooks.push(newNotebook);
};

export const saveQuestion = (_notebook: Notebook, question: Question) => {
    const { id } = _notebook;

    if (!id) return;

    const newId = _newId();
    const newQuestion: Question = {
        ...question,
        id: newId,
        date_created: new Date(),
    };

    const notebook = notebooks.find(x => x.id === id);
    notebook?.questions?.push(newQuestion);

    return new Promise((resolve, reject) => {
        setTimeout(() => {
            return resolve(notebook);
        }, 750);
    });
};
