import * as React from 'react';
import {
  Box,
  Chip, Button, TextField,
  Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle,
  Typography
} from '@mui/material';
import { MuiChipsInput } from 'mui-chips-input';
import { Category } from '../../types/Category';

export type BlankQuestionEntryFormResponse = {
    text: string,
    selections: string[],
    categories: Category[]
};

export type BlankQuestionEntryFormProps = {
  title: string,
  description?: string,
  inputPlaceholder?: string,
  inputType?: string,
  isOpen: boolean,
  autocomplete?: boolean,
  rows?: number
  onClose: (isProceed: boolean, data?: BlankQuestionEntryFormResponse) => void
};

enum Modes {
    QuestionEntry,
    BlankSelection
}

const defaultValues = {
  title: 'Untitled Form',
  description: 'Enter content for this question.',
  inputPlaceholder: 'Enter text',
  inputType: 'text',
  autocomplete: false,
  isOpen: false
};

const isDebugMode = false;

const BlankQuestionEntryForm: React.FC<BlankQuestionEntryFormProps> = (props) => {
    const {
        title,
        description,
        inputPlaceholder,
        inputType,
        autocomplete,
        rows = 1,
        isOpen,
        onClose
    } = props;
    const [mode, setMode] = React.useState<Modes>(Modes.QuestionEntry);
    const [TextFieldValue, setTextFieldValue] = React.useState('');
    const [selections, setSelections] = React.useState<string[]>([]);
    const [categories, setCategories] = React.useState<string[]>([])
    const [open, setOpen] = React.useState(isOpen);

    const handleClose = (isProceed: boolean) => {
        if (!isProceed) {
            onClose(false);
            setOpen(false);
            setMode(Modes.QuestionEntry);
            return;
        }

        onClose(true, {
            text: TextFieldValue,
            selections: selections,
            categories: categories.map(x => ({ name: x }))
        });
        setOpen(false);
        setMode(Modes.QuestionEntry);
    };

    const handleTextFieldChange = (e: any) => {
        setTextFieldValue(e.target.value);
    }

    const handleCategoryChange = (newCategories: string[]) => {
        if (newCategories && newCategories.length > categories.length) {
            const lastItem = newCategories[newCategories.length - 1];
            // ignore if tag is already in the list
            if (categories.map(x => x.toLowerCase()).includes(lastItem.toLowerCase())) return;
        }

        setCategories(newCategories);
    }

    const reset = () => {
        if (!isOpen) return;
        setTextFieldValue('');
        setSelections([]);
        setCategories([]);
    }

    React.useEffect(() => {

    }, []);

    React.useEffect(() => {
        reset();
        setOpen(isOpen);
    }, [isOpen])

    React.useEffect(() => {

    }, [selections]);

    const getOverlappingHighlight = (range: Range) => {
        const ancestor = range.commonAncestorContainer;
        const spans = document.querySelectorAll('span.highlighted-text');
        for (let i = 0; i < spans.length; i++) {
            if (range.intersectsNode(spans[i])) {
                return spans[i];
            }
        }

        return null;
    };

    const removeHighlight = (highlight: Element) => {
        const range = document.createRange();
        range.selectNodeContents(highlight);
        const selection = window.getSelection();

        if (!selection) return;
        selection.removeAllRanges();
        selection.addRange(range);
        highlight.outerHTML = highlight.innerHTML;
    };

    // TO DO: There is still a BUG on iPad for overlapping items
    const highlightText = () => {
        const selection = window.getSelection();
        if (!selection || selection.toString().trim() === '') return;

        const range = selection.getRangeAt(0);
        const existingHighlight = getOverlappingHighlight(range);

        if (existingHighlight) {
            removeHighlight(existingHighlight);
        }  {
            const newSelection = selection.toString().trim();
            const newNode = document.createElement('span');
            newNode.className = 'highlighted-text';
            newNode.style.backgroundColor = 'yellow';
            range.surroundContents(newNode);
            selections.push(newSelection)
            setSelections([...selections]);
        }

        // Clear selection BUG on iPad
        if (selection.empty) {
            selection.empty();
        } else if (selection.removeAllRanges) {
            selection.removeAllRanges();
        }
    };

    const breakIntoLines = (question: string): string[] => {
        return question.split('\n');
    };

    return (
        <div>
        <Dialog
            open={open}
            disableEscapeKeyDown={true}>
            <DialogTitle
            sx={{
                minWidth: '345px',
                width: '75vw',
            }}
            >{title}</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    { mode === Modes.QuestionEntry ?
                        <Typography>Enter the text you want to use for your question.</Typography> :
                        <Typography>Select at least one text for this question.</Typography>
                    }
                </DialogContentText>

                { /* QUESTION ENTRY STATE */ }
                { mode === Modes.QuestionEntry ?
                    <React.Fragment>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="name"
                            label={inputPlaceholder || defaultValues.inputPlaceholder}
                            type={inputType || defaultValues.inputType}
                            fullWidth
                            variant="outlined"
                            autoComplete={autocomplete ? 'on' : 'off'}
                            spellCheck="false"
                            multiline={rows > 1}
                            rows={rows}
                            onChange={handleTextFieldChange}
                            value={TextFieldValue}
                        />
                        <MuiChipsInput value={categories} onChange={handleCategoryChange} placeholder='Enter tags' />
                    </React.Fragment>
                 : null }

                { /* BLANK SELECTION STATE */ }
                { mode === Modes.BlankSelection ? (
                    <Box sx={{ padding: '16.5px 14px'}}>
                        { breakIntoLines(TextFieldValue).map((line, idx: number) => {
                            return <Typography key={idx} onMouseUp={highlightText} onTouchEnd={highlightText} sx={{ marginBottom: '12px' }}>{line}</Typography>;
                        })}
                        <Box>
                            { categories && categories.length ? categories.map((x: string, idx: number) => {
                                return <Chip label={x} size="small" key={idx} sx={{ margin: '24px 4px 4px 0' }} />;
                            }) : null }
                        </Box>
                    </Box>
                ) : null}

                { /* ONLY ON DEBUG MODE */}
                { isDebugMode ? <ul>
                    { selections.map((x:string, idx: number) => {
                        return <li key={idx}>{x}</li>;
                    })}
                </ul> : null }
            </DialogContent>
            <DialogActions>
            <Button onClick={() => handleClose(false)}>Cancel</Button>
            { mode === Modes.QuestionEntry ? <Button disabled={!TextFieldValue.trim()} onClick={() => setMode(Modes.BlankSelection)}>Next</Button> : null }
            { mode === Modes.BlankSelection ? <Button disabled={!selections.length} onClick={() => handleClose(true)}>Save</Button> : null }
            </DialogActions>
        </Dialog>
        </div>
    );
}

export default BlankQuestionEntryForm;
