import * as React from 'react';
import {
    Button, Chip, Paper,
    Table, TableBody, TableCell, TableContainer, TableHead, TablePagination, TableRow
} from '@mui/material';
import {
    Edit,
    Delete
} from '@mui/icons-material';

import { Notebook } from '../../types/Notebook';
import { formatDate } from '../../utils/formatter';

import { Category } from '../../types/Category';

interface Column {
  id: 'question' | 'dateCreated' | 'categories';
  label: string;
  minWidth?: number;
  align?: 'right';
  format?: (value: any) => any;
}

const columns: readonly Column[] = [
  { id: 'question', label: 'Question', minWidth: 300 },
  {
    id: 'dateCreated',
    label: 'Created',
  },
  {
    id: 'categories',
    label: 'Categories',
  }
];

interface Data {
  question: string;
  selections: string[];
  dateCreated: string;
  categories: Category[];
}

function createData(question: string, selections: string[] | undefined,dateCreated?: Date, categories?: Category[]): Data {
    return {
        question: question,
        selections: selections ?? [],
        dateCreated: formatDate(dateCreated ?? ''),
        categories: categories ?? []
    };
}

type QuestionsTableProps = {
    notebook: Notebook;
    displayBlanks?: boolean;
}

const QuestionsTable: React.FC<QuestionsTableProps> = (props) => {
    const { notebook, displayBlanks = true } = props;
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);
    const [rows, setRows] = React.useState<Data[]>([]);
    const [currentNotebook, setCurrentNotebook] = React.useState<Notebook | undefined>(undefined);

    const handleChangePage = (event: unknown, newPage: number) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    const createRows = () => {
        if (!currentNotebook) return;
        if (!currentNotebook.questions) return;

        const rows = currentNotebook.questions.map(x => {
            return createData(x.text, x.selections, x.date_created, x.categories);
        });

        setRows(rows);
    }

    React.useEffect(() => {
        setCurrentNotebook(notebook);
    }, [notebook]);

    React.useEffect(() => {
        createRows();
    }, [currentNotebook]);

    return (
        <Paper sx={{ width: '100%', overflow: 'hidden' }}>
        <TableContainer sx={{ maxHeight: '50vh' }}>
            <Table stickyHeader aria-label="sticky table">
            <TableHead>
                <TableRow>
                {columns.map((column) => (
                    <TableCell
                        key={column.id}
                        align={column.align}
                        style={{ minWidth: column.minWidth }}
                        >
                        {column.label}
                    </TableCell>
                ))}
                <TableCell>Action</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {rows
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row: any, idx: number) => {
                    return (
                        <TableRow hover role="checkbox" tabIndex={-1} key={idx}>
                            {columns.map((column) => {
                                const value = row[column.id];

                                if (column.id === 'categories') {
                                    const chips = value.map((x: Category, idx: number) => {
                                        return <Chip label={x.name} size="small" key={idx} sx={{ margin: '0 4px 4px 0' }} />;
                                    });
                                    return (
                                        <TableCell key={column.id} sx={{ verticalAlign: 'top' }}>
                                            {chips ?? ''}
                                        </TableCell>
                                    );
                                }

                                if (column.id === 'question') {
                                    let text = value;
                                    const selections = rows[idx].selections;
                                    const segments = [];
                                    let currentPosition = 0;

                                    // iterate through selections and text
                                    selections.forEach(selection => {
                                        const startIndex = text.indexOf(selection, currentPosition);
                                        if (startIndex !== -1) {
                                            // Add text before the selection
                                            segments.push(text.substring(currentPosition, startIndex));

                                            // Add the highlighted selection
                                            segments.push(
                                                <span key={startIndex} className="highlight">
                                                    {selection}
                                                </span>
                                            );

                                            currentPosition = startIndex + selection.length;
                                        }
                                    });

                                    // Add remaining text after the last selection
                                    segments.push(text.substring(currentPosition));

                                    // replace parts of the text with _
                                    selections.forEach(x => {
                                        text = text.replace(x, '_'.repeat(x.length));
                                    });

                                    return (
                                        <TableCell key={column.id} align={column.align} sx={{ verticalAlign: 'top', wordWrap: 'break-word', overflowWrap: 'break-word', maxWidth: '500px' }}>
                                            {

                                            }
                                            {
                                                displayBlanks ? text :
                                                segments.map((segment, idx) => (
                                                    <React.Fragment key={idx}>{segment}</React.Fragment>
                                                ))
                                            }
                                        </TableCell>
                                    );
                                }

                                return (
                                    <TableCell key={column.id} align={column.align}  sx={{ verticalAlign: 'top' }}>
                                        {column.format && typeof value === 'number'
                                            ? column.format(value)
                                            : value}
                                    </TableCell>
                                );
                            })}
                            <TableCell  sx={{ verticalAlign: 'top' }}>
                                <Button startIcon={<Edit />}>Edit</Button>
                                <Button startIcon={<Delete />} color="error">Delete</Button>
                            </TableCell>
                        </TableRow>
                    );
                })}
            </TableBody>
            </Table>
        </TableContainer>
        <TablePagination
            rowsPerPageOptions={[10, 25, 100]}
            component="div"
            count={rows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
        />
        </Paper>
    );
}

export default QuestionsTable;
