import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import DashboardIcon from '@mui/icons-material/Dashboard';
import BarChartIcon from '@mui/icons-material/BarChart';
import { Logout, Checklist, DocumentScanner } from '@mui/icons-material';
import AssignmentIcon from '@mui/icons-material/Assignment';

type DrawerRoute  = {
    label: string,
    path: string,
    icon?: React.ReactElement
};
const drawerRoutes: DrawerRoute[] = [
    {
        label: 'Dashboard',
        path: '/dashboard',
        icon: <DashboardIcon />
    },
    {
        label: 'Prepare Questions',
        path: '/prepare/notebooks',
        icon: <DocumentScanner />
    },
    {
        label: 'Review',
        path: '/review',
        icon: <Checklist />
    },
    {
        label: 'Reports',
        path: '/reports',
        icon: <BarChartIcon />
    },
    {
        label: 'Sign Out',
        path: '/signout',
        icon: <Logout />
    }
]

export const MainListItems = () => {
    const navigate = useNavigate();
    const listItems = drawerRoutes.map((r, idx) => {
        return (
            <ListItemButton key={idx} onClick={() => {
                navigate(r.path);
            }}>
                <ListItemIcon>
                    {r.icon ? r.icon : null}
                </ListItemIcon>
                <ListItemText primary={r.label} />
            </ListItemButton>
        );
    });

    return (
        <React.Fragment>
            {listItems}
        </React.Fragment>
    )
};

export const secondaryListItems = (
  <React.Fragment>
    <ListSubheader component="div" inset>
      Favorites
    </ListSubheader>
    <ListItemButton>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="2023 Bar Reviewer" />
    </ListItemButton>
    <ListItemButton>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Taxation Reviewer" />
    </ListItemButton>
  </React.Fragment>
);
