import * as React from 'react';

import {
    Grid,
    CircularProgress,
    Typography
} from '@mui/material';

type CircularLoaderProps = {
    message?: string;
}

const CircularLoader: React.FC<CircularLoaderProps> = (props) => {
    const { message = "Loading..." } = props;
    return (
        <Grid container justifyContent="center" alignItems="center" sx={{ minHeight: '250px' }}>
            <Typography variant="h4" sx={{ marginRight: '32px'}}>
                {message}
            </Typography>
            <Typography>
                <CircularProgress />
            </Typography>
        </Grid>
    );
}

export default CircularLoader;
